







































import ut from '../../utils/index.js';
export default {
	data() {
		return {
			swipeList: ['../../static/index/banner.jpg','../../static/index/banner.jpg'],
			indicatorDots: true,
			indicatorcolor: 'white',
			indicatoractivecolor:'#FEC200',
			autoplay: true,
			interval: 5000,
			duration: 1000,
			circular: true,
			typeindex:0,
			typelist:[
				{
					name:'马桶疏通',
					money:"100.00"
				},
				{
					name:'马桶疏通',
					money:"100.00"
				},
				{
					name:'马桶疏通',
					money:"100.00"
				},
				{
					name:'马桶疏通',
					money:"100.00"
				}
			]
		}
	},
	onLoad(opt) {
		this.req_detail(opt._id)
	},
	methods: {
		back_index_build(){
			wx.reLaunch({
				url: '../index/build'
			});
		},
		cg_typeindex(index){
			this.typeindex=index;
		},
		go_home_pay(){
			wx.navigateTo({
				url: '../home/pay'
			})
		},
		req_detail(id){
			ut.request({
				data: {
					classid:id
				},
				url: "service/detail"
			}).then(data=>{
				this.recomlist=data
			})
		}
	}
}



